import * as React from 'react';
import { observer } from 'mobx-react';
import { VPage, Page, Image } from 'tonva';
import { CMe } from './CMe';
import QRCode from 'qrcode.react';
import { consts } from '../consts';
import logo from '../images/logonew.png';
import { ClickDownLoad } from './DownLoadImaige';

export class VInvitationCode extends VPage<CMe> {

    async open(code: string) {
        this.openPage(this.page, { code: code });
    }

    save = () => {
        var Qr = document.getElementById('qrid');
        ClickDownLoad(Qr);
    }

    private page = observer((param: any) => {
        let url = "http://agent.jkchemical.com?code=" + param.code;

        return <Page header='' headerClassName={consts.headerClass}>
            <div className="text-center" id="qrid">
                <Image className="my-4" style={{ width: 'auto', height: '25rem' }} src={logo} />
                <QRCode
                    value={url}  //value
                    size={100} //
                    fgColor="#000000"  //
                />
                <div>{param.code}</div>
                <div>
                    <button type="button" className="btn  flex-grow-1 mx-3 my-1" onClick={this.save} ></button>
                    <button type="button" className="btn  flex-grow-1 mx-3 my-1" onClick={this.save} ></button>
                </div>
            </div>
        </Page >
    })
}
